<script>
  import network from '../networkUtil'
  import {MDCTextField} from '@material/textfield'
  import {MDCSlider} from '@material/slider'
  import {MDCSelect} from '@material/select'
  
  export default {
    methods: {
      displayDetails (id, cached) {
        if (this.network.online || cached) {
          this.$router.push({name: 'detail', params: { id: id }})
        }
      }
    },
    data () {
      return {
        'pictures': [],
        'spinner_active': true,
        'network': network,
        'fasting': 4.6,
        'postprandial': 8.5,
        'slider_value': 0
      }
    },
    mounted () {
      var contextHolder = this
      var now = new Date()

      const year = new MDCSelect(document.querySelector('.year'))
      year.listen('change', () => {
        alert(`Selected option at index ${year.selectedIndex} with value "${year.value}"`)
      })
      year.value = now.getFullYear()
      const month = new MDCSelect(document.querySelector('.month'))
      month.listen('change', () => {
        alert(`Selected option at index ${month.selectedIndex} with value "${month.value}"`)
      })
      month.value = now.getMonth() + 1
      const date = new MDCSelect(document.querySelector('.date'))
      date.listen('change', () => {
        alert(`Selected option at index ${date.selectedIndex} with value "${date.value}"`)
      })
      date.value = now.getDate()
      const hour = new MDCSelect(document.querySelector('.hour'))
      hour.listen('change', () => {
        alert(`Selected option at index ${hour.selectedIndex} with value "${hour.value}"`)
      })
      hour.value = now.getHours()

      var remarks = new MDCTextField(document.querySelector('.remarks'))
      console.log(remarks)

      const slider = new MDCSlider(document.querySelector('.mdc-slider'))
      slider.listen('MDCSlider:input', function (detail) {
        contextHolder.slider_value = Math.round(slider.value * 10) / 10
      })

      // Fetch the latest data.
      var resolveURL = function (paginate) {
        return new Promise((resolve, reject) => {
          var statement = ''
          if (paginate == null) {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true'
          } else {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true&pageToken=' + paginate
          }
          resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts' + statement)
        })
      }
      function fetchList (paginate) {
        return resolveURL(paginate)
          .then((url) => network.promisedRequest('GET', url))
          .then((rawResponse) => {
            var response = JSON.parse(rawResponse)
            console.log(response)
            response.items.forEach((postAbstract) => {
              if (postAbstract.images[0] != null) {
                postAbstract.image = postAbstract.images[0].url.replace('/s1600/', '/w320/') // downsample image size
              }
              postAbstract.cached = false
              if ('caches' in window) {
                caches.open('blog-content').then((cache) => cache.match(postAbstract.selfLink + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'))
                  .then((response) => {
                    if (typeof response !== 'undefined') {
                      postAbstract.cached = true
                      return response.json().then((json) => {
                        var exp = /src="(.*?)"/g
                        var list = []
                        var match
                        do {
                          match = exp.exec(json.content)
                          if (match) {
                            list.push(match[1])
                          }
                        } while (match)
                        console.log(list)
                        caches.open('manga-images').then((cache) => cache.keys())
                          .then((keys) => {
                            console.log(keys)
                            list.forEach((key) => {
                              // function to find imageurl in cache
                              if (keys.findIndex((request) => {
                                var url = request.url.replace(/(s1600|w320|w400|w500|w600|w700|w800|w900|w1000|w1200|w1400|w1600)+/g, 's1600')
                                return (url === key)
                              }) === -1) {
                                postAbstract.cached = false
                                console.log(key, ' is not found!')
                              }
                            })
                          })
                      })
                    }
                  })
              }
              contextHolder.pictures.push(postAbstract)
            })
            if (response.nextPageToken != null) {
              return fetchList(response.nextPageToken)
            }
          })
      }
      fetchList().then(() => { contextHolder.spinner_active = false })
    }
  }
</script>
