<template>
    <div class="mdc-layout-grid">
    <!-- alert cards-->
<section class="mdc-card mdc-card--outlined">
  <div class="mdc-card__media mdc-card__media--square">
    <div class="mdc-card__media-content">你的教練</div>
  </div>
  <p>
  做得好！餐後度數比上星期進步
  </p>
  <div class="mdc-card__actions">
    <div class="mdc-card__action-buttons">
      <button class="mdc-button mdc-card__action mdc-card__action--button">知道了</button>
    </div>
  </div>
    <!-- recent readings-->
</section>
<section class="hero">
      <section class="mdc-layout-grid__inner ">

    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-typography--headline6">飯前</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-typography--headline6">飯後</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-typography--headline2">{{fasting}}</h5>
    	<h5 class="mdc-layout-grid__cell mdc-layout-grid__cell--span-2 mdc-layout-grid__cell--span-4-tablet mdc-typography--headline2">{{postprandial}}</h5>
	</section>
  </section>
    <div>
    <router-link class="mdc-fab mdc-fab--extended" to="/glucose/new">
      <span class="material-icons mdc-fab__icon">add</span>
      <span class="mdc-fab__label">新增度數</span>
    </router-link>
        <button class="mdc-button mdc-button--raised">
          <i class="material-icons mdc-button__icon" aria-hidden="true">favorite</i>
          低血糖處理
        </button>
        <button class="mdc-button mdc-button--raised">
          <i class="material-icons mdc-button__icon" aria-hidden="true">favorite</i>
          高血糖處理
        </button>
    </div>
    <div class="mdc-grid-list mdc-grid-list--tile-aspect-3x4 mdc-grid-list--with-icon-align-end">
      <ul class="mdc-grid-list__tiles">
      <li class="mdc-grid-tile" v-for="picture in this.pictures" v-if="network.online || picture.cached" @click="displayDetails(picture.id, picture.cached)">
        <div class="mdc-grid-tile__primary">
          <div class="mdc-grid-tile__primary-content"
            v-bind:style="'background-image : url('+picture.image+');'">
            <div v-if="!picture.cached" class="overlay"></div>
          </div>
        </div>
        <span class="mdc-grid-tile__secondary">
          <i v-if="!picture.cached" class="mdc-grid-tile__icon material-icons">cloud_download</i>
          <i v-if="picture.cached" class="mdc-grid-tile__icon material-icons">done</i>
          <span class="mdc-grid-tile__title mdc-typography--caption">{{ picture.title }}</span>
        </span>
      </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import network from '../networkUtil'
  export default {
    methods: {
      displayDetails (id, cached) {
        if (this.network.online || cached) {
          this.$router.push({name: 'detail', params: { id: id }})
        }
      }
    },
    data () {
      return {
        'pictures': [],
        'spinner_active': true,
        'network': network,
        'fasting': 4.6,
        'postprandial': 8.5
      }
    },
    mounted () {
      var contextHolder = this
      // Fetch the latest data.
      var resolveURL = function (paginate) {
        return new Promise((resolve, reject) => {
          var statement = ''
          if (paginate == null) {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true'
          } else {
            statement = '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI&fetchBodies=false&status=live&fetchImages=true&pageToken=' + paginate
          }
          resolve('https://www.googleapis.com/blogger/v3/blogs/' + process.env.BLOG_CODE + '/posts' + statement)
        })
      }
      function fetchList (paginate) {
        return resolveURL(paginate)
          .then((url) => network.promisedRequest('GET', url))
          .then((rawResponse) => {
            var response = JSON.parse(rawResponse)
            console.log(response)
            response.items.forEach((postAbstract) => {
              if (postAbstract.images[0] != null) {
                postAbstract.image = postAbstract.images[0].url.replace('/s1600/', '/w320/') // downsample image size
              }
              postAbstract.cached = false
              if ('caches' in window) {
                caches.open('blog-content').then((cache) => cache.match(postAbstract.selfLink + '?key=AIzaSyAs38-G2Y_bJaZI-z2z2OjT9_xtw6F32FI'))
                  .then((response) => {
                    if (typeof response !== 'undefined') {
                      postAbstract.cached = true
                      return response.json().then((json) => {
                        var exp = /src="(.*?)"/g
                        var list = []
                        var match
                        do {
                          match = exp.exec(json.content)
                          if (match) {
                            list.push(match[1])
                          }
                        } while (match)
                        console.log(list)
                        caches.open('manga-images').then((cache) => cache.keys())
                          .then((keys) => {
                            console.log(keys)
                            list.forEach((key) => {
                              // function to find imageurl in cache
                              if (keys.findIndex((request) => {
                                var url = request.url.replace(/(s1600|w320|w400|w500|w600|w700|w800|w900|w1000|w1200|w1400|w1600)+/g, 's1600')
                                return (url === key)
                              }) === -1) {
                                postAbstract.cached = false
                                console.log(key, ' is not found!')
                              }
                            })
                          })
                      })
                    }
                  })
              }
              contextHolder.pictures.push(postAbstract)
            })
            if (response.nextPageToken != null) {
              return fetchList(response.nextPageToken)
            }
          })
      }
      fetchList().then(() => { contextHolder.spinner_active = false })
    }
  }
</script>
<style scoped>
@import "../../node_modules/@material/typography/dist/mdc.typography.css";
@import "../../node_modules/@material/layout-grid/dist/mdc.layout-grid.css";
@import "../../node_modules/@material/grid-list/dist/mdc.grid-list.css";
@import "../../node_modules/@material/linear-progress/dist/mdc.linear-progress.css";
@import "../../node_modules/@material/button/dist/mdc.button.css";
@import "../../node_modules/@material/card/dist/mdc.card.css";
@import "../../node_modules/@material/fab/dist/mdc.fab.css";

  .mdc-grid-tile__primary-content {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  .mdc-grid-tile {
    --mdc-grid-list-tile-width: 300px;
  }
  .mdc-grid-tile__secondary, .overlay {
    background: rgba(0, 0, 0, 0.5);
  }
  .overlay {
    height: -webkit-fill-available;
  }
  .spinner-bar {
    text-align: center;
  }
  .hero {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: center;
      height: 360px;
      min-height: 360px;
  }
</style>


