<template>
  <div class="mdc-typography mdc-theme--dark">
    <header class="mdc-toolbar mdc-toolbar--fixed">
      <div class="mdc-toolbar__row">
        <section class="mdc-toolbar__section mdc-toolbar__section--align-start">
          <a href="#" class="material-icons mdc-toolbar__menu-icon menu">menu</a>
          <span class="mdc-toolbar__title">{{ this.title }}</span>
        </section>
      </div>
    </header>
    <aside class="mdc-temporary-drawer mdc-typography">
      <nav class="mdc-temporary-drawer__drawer">
        <div class="mdc-persistent-drawer__toolbar-spacer"></div>
        <nav id="icon-with-text-demo" class="mdc-temporary-drawer__content mdc-list">
          <router-link class="mdc-list-item mdc-temporary-drawer--selected" to="/" @click.native="hideMenu" href="#">
            <i class="material-icons mdc-list-item__start-detail" aria-hidden="true">opacity</i>血糖
          </router-link>
          <a class="mdc-list-item" href="#">
            <i class="material-icons mdc-list-item__start-detail" aria-hidden="true">local_dining</i>飲食
          </a>
        </nav>
      </nav>
    </aside>
    <main class="mdc-toolbar-fixed-adjust">
      <div class="page-content">
        <router-view></router-view>
      </div>
      <div class="mdc-snackbar" id="offline_toast"
           aria-live="assertive"
           aria-atomic="true"
           aria-hidden="true">
        <div class="mdc-snackbar__text"></div>
        <div class="mdc-snackbar__action-wrapper">
          <button type="button" class="mdc-snackbar__action-button"></button>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { MDCTemporaryDrawer } from '@material/drawer'
import { MDCSnackbar } from '@material/snackbar'
import network from './networkUtil'
import Notif from './notifUtil'
const firebase = require('./firebase').Firebase
export default {
  name: 'app',
  data () {
    return {
      'title': process.env.APP_TITLE,
      'usedMBytes': 0,
      'grantedMBytes': 0
    }
  },
  methods: {
    hideMenu: function () {
      var drawer = new MDCTemporaryDrawer(document.querySelector('.mdc-temporary-drawer'))
      drawer.open = false
    }
  },
  mounted () {
    const drawer = new MDCTemporaryDrawer(document.querySelector('.mdc-temporary-drawer'))
    const snackbar = new MDCSnackbar(document.querySelector('#offline_toast'))
    document.querySelector('.menu').addEventListener('click', () => { drawer.open = true })

    var OneSignal = window.OneSignal || []
    OneSignal.push(['init', {
      appId: 'b69cd6a5-914d-4720-888b-ce7b800f88f6',
      autoRegister: true, /* Set to true to automatically prompt visitors */
      notifyButton: {
        enable: true /* Set to false to hide */
      }
    }])
    var contextHolder = this
    window.addEventListener('load', function () {
      function updateOnlineStatus (event) {
        const dataObj = {
          message: navigator.onLine ? '網路已重新連線' : '網路已斷線，進入離線模式',
          actionText: 'OK',
          actionHandler: function () {
            console.log('OKED')
          }
        }
        snackbar.show(dataObj)
        network.online = navigator.onLine
      }

      window.addEventListener('online', updateOnlineStatus)
      window.addEventListener('offline', updateOnlineStatus)
    })
    navigator.webkitTemporaryStorage.queryUsageAndQuota(
      function (usedBytes, grantedBytes) {
        contextHolder.usedMBytes = usedBytes / 1024 / 1024
        contextHolder.grantedMBytes = grantedBytes / 1024 / 1024
      },
      function (e) { console.log('Error', e) }
    )
    // Initialize Firebase
    var config = {
      apiKey: 'AIzaSyC2ye04Kbt002qR8dMeO-L1V1RFNqFzovc',
      authDomain: 'mangaclub-1509968179920.firebaseapp.com',
      databaseURL: 'https://mangaclub-1509968179920.firebaseio.com',
      projectId: 'mangaclub-1509968179920',
      storageBucket: '',
      messagingSenderId: '294370342368'
    }
    firebase.initializeApp(config)
    if ('serviceWorker' in navigator && 'PushManager' in window) {
      window.addEventListener('load', function () {
        console.log('Service Worker and Push is supported')
        const messaging = firebase.messaging()
        navigator.serviceWorker.getRegistration()
          .then(function (swReg) {
            console.log('Service Worker is registered', swReg)
            var notif = new Notif(swReg)
            if (!notif.isSubscribed) {
              notif.subscribeUser()
              messaging.useServiceWorker(swReg)
            }
          })
          .catch(function (error) {
            console.error('Service Worker Error', error)
          })
        messaging.requestPermission()
          .then(function () {
            console.log('Notification permission granted.')
            // TODO(developer): Retrieve an Instance ID token for use with FCM.
            // ...
          })
          .catch(function (err) {
            console.log('Unable to get permission to notify.', err)
          })
        messaging.getToken()
          .then(function (currentToken) {
            console.log('getting token')
            console.log(currentToken)
            if (currentToken) {
              console.log('https://iid.googleapis.com/iid/v1/' + currentToken + '/rel/topics/general')
              fetch('https://iid.googleapis.com/iid/v1/' + currentToken + '/rel/topics/general', {
                method: 'POST',
                headers: new Headers({
                  'Authorization': 'key=AAAARInW9eA:APA91bEuUhUpVbgVykynjwkeRX9ZuWYEGRZHECYvGuE1zRnwVX-Z_se-JCnb52gZi9I7j-1nJySgE7u0f8dVI5vJc5zrRlH25XLRnto8mX_aYX5d7ReYkLIbyZcvu9PEdYreFZ2iMvf1'
                })
              }).then(response => {
                if (response.status < 200 || response.status >= 400) {
                  throw new Error('Error subscribing to topic: ' + response.status + ' - ' + response.text())
                }
                console.log('Subscribed to topic')
              }).catch(error => {
                console.error(error)
              })
              // updateUIForPushEnabled(currentToken)
            } else {
              // Show permission request.
              console.log('No Instance ID token available. Request permission to generate one.')
              // Show permission UI.
              // updateUIForPushPermissionRequired()
              // setTokenSentToServer(false)
            }
          })
          .catch(function (err) {
            console.log('An error occurred while retrieving token. ', err)
            // showToken('Error retrieving Instance ID token. ', err)
            // setTokenSentToServer(false)
          })
        messaging.onTokenRefresh(function () {
          messaging.getToken()
            .then(function (refreshedToken) {
              console.log('Token refreshed.')
              // Indicate that the new Instance ID token has not yet been sent to the
              // app server.
              // setTokenSentToServer(false)
              // Send Instance ID token to app server.
              // sendTokenToServer(refreshedToken)
              // ...
            })
            .catch(function (err) {
              console.log('Unable to retrieve refreshed token ', err)
              // showToken('Unable to retrieve refreshed token ', err)
            })
        })
      })
    } else {
      console.warn('Push messaging is not supported')
    }
  }
}
</script>

<style>
@import "../node_modules/@material/theme/dist/mdc.theme.css";
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import "../node_modules/@material/toolbar/dist/mdc.toolbar.css";
@import "../node_modules/@material/drawer/dist/mdc.drawer.css";
@import "../node_modules/@material/list/dist/mdc.list.css";
@import "../node_modules/@material/snackbar/dist/mdc.snackbar.css";
@import "../node_modules/@material/typography/dist/mdc.typography.css";

  :root {
    --mdc-theme-primary: #ff5d4b; 
    --mdc-theme-secondary: #EF9A9A; 
    background-color: #f3f3f3;
  }
  img {
    max-width: 100%;
  }
</style>



